.contactus-section {
  position: relative; /* important for ::after positioning */
  border-radius: 24px;
  background: var(--new, linear-gradient(130deg, #492FB2 -1.74%, #ADADAD 108.02%));
  padding: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden; /* keeps the after image inside the box */
}

.contactus-section p {
  color: #FFF;
  font-family: Urbanist;
  font-size: 28px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  margin: 0px;
  width: 50%;
}

/* Add image with ::after */
.contactus-section::after {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 50%; /* adjust how much space image takes */
  background: url('../../../../../assets/images/ias-contact-rings.png') no-repeat center right;
  background-size: cover; /* make it responsive */
  border-top-right-radius: 24px;
  border-bottom-right-radius: 24px;
}
